<template>
  <section class="memory-game">
    <nav>
      <div class="moves">Moves: {{ count }}</div>
      <button @click="reset" class="reset-button"><span>Reset</span></button>
      <div class="time">Time: {{ min }}:{{ sec }}</div>
      <div class="difficulty">
          <div class="label-input">
            <input type="radio" id="normal" name="difficulty" value="normal" v-model="difficulty" @click="reset">
            <label for="normal">Normal</label>
          </div>
          <div class="label-input">
            <input type="radio" id="hard" name="difficulty" value="hard" v-model="difficulty" checked @click="reset">
            <label for="hard">Hard</label>
          </div>
      </div>
    </nav>
    <div v-for="(card, i) in memoryCards" :key="i" @click='flipCard(card)' class="memory-card" v-bind:class="{'flip': card.isFlipped, 'matched': card.isMatched}">
      <img :src="require(`@/assets/img/${card.icon}`)" alt="" class="front-face">
      <img src="@/assets/img/dc-no-bg.png" alt="" class="back-face">
    </div>
  </section>
</template>

<script>
import cards from '@/cards.json'
import _ from 'lodash'

export default {
  data () {
    return {
      memoryCards: cards,
      flippedCards: [],
      normalDeck: [],
      hardDeck: [],
      start: false,
      finish: false,
      difficulty: 'hard',
      count: 0,
      totalTime: {
        minutes: 0,
        seconds: 0
      }
    }
  },
  computed: {
    sec () {
      return ('0' + this.totalTime.seconds).slice(-2)
    },
    min () {
      return ('0' + this.totalTime.minutes).slice(-2)
    }
  },

  created () {
    this.memoryCards = this.memoryCards.concat(_.cloneDeep(this.memoryCards))
    this.memoryCards.sort((a, b) => (a.icon > b.icon) ? 1 : ((b.icon > a.icon) ? -1 : 0))
    this.normalDeck = this.memoryCards.slice(0, 12)
    this.hardDeck = this.memoryCards
    this.reset()
  },

  methods: {

    flipCard (card) {
      if (card.isMatched || card.isFlipped || this.flippedCards.length === 2) return

      if (!this.start) {
        this.startTimer()
      }

      card.isFlipped = !card.isFlipped

      if (this.flippedCards.length < 2) {
        this.flippedCards.push(card)
      }
      if (this.flippedCards.length === 2) {
        this.matchCards()
      }
    },

    matchCards () {
      this.count++
      if (this.flippedCards[0].icon === this.flippedCards[1].icon) {
        setTimeout(() => {
          // eslint-disable-next-line no-return-assign
          this.flippedCards.forEach(card => card.isMatched = true)
          this.flippedCards = []

          if (this.memoryCards.every(card => card.isMatched === true)) {
            clearInterval(this.interval)
            this.finish = true
          }
        }, 400)
      } else {
        setTimeout(() => {
        // eslint-disable-next-line no-return-assign
          this.flippedCards.forEach(card => card.isFlipped = false)
          this.flippedCards = []
        }, 800)
      }
    },

    startTimer () {
      this.tick()
      this.interval = setInterval(this.tick, 1000)
      this.start = true
    },

    tick () {
      if (this.totalTime.seconds !== 59) {
        this.totalTime.seconds++
        return
      }

      this.totalTime.minutes++
      this.totalTime.seconds = 0
    },

    reset () {
      clearInterval(this.interval)
      this.memoryCards.forEach((card) => {
        card.isFlipped = false
        card.isMatched = false
      })

      setTimeout(() => {
        if (this.difficulty === 'normal') {
          this.memoryCards = this.normalDeck
        } else if (this.difficulty === 'hard') {
          this.memoryCards = this.hardDeck
        }
        this.memoryCards = _.shuffle(this.memoryCards)
        this.finish = false
        this.flippedCards = []
        this.count = 0
        this.start = false
        this.totalTime.seconds = 0
        this.totalTime.minutes = 0
      }, 400)
    }

  }
}
</script>
